import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import { GatsbyImage } from 'gatsby-plugin-image'
import { css } from '@emotion/react'
import styled from '@emotion/styled'

const imgSize = "144px"

const cssBox = css`
  margin-bottom: 1rem;
  max-width: 800px;
  border-radius: 10px;
  overflow: hidden;

`
const cssImageWrapper = css`
  width: ${imgSize};
  height: ${imgSize};
  float: left;
  padding: 0px;
  
  overflow: hidden;
  position: relative;

  .gatsby-image-wrapper {
    width: 100%;
    height: ${imgSize};
    top: 0px;
    position: absolute;
    object-fit: cover;
  }
`
const HoverBox = styled.div`
    &:hover {
    background-color: #ddd;
    opacity: 0.7;
    }
    a {
        text-decoration: none;
        color: black;
    }
    box-shadow: rgb(0 0 0 / 20%) 0px 3px 1px -2px, rgb(0 0 0 / 14%) 0px 2px 2px 0px, rgb(0 0 0 / 12%) 0px 1px 5px 0px
`

const Title = styled.div`
  font-size: 1rem;
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  `
const Description = styled.div`
  font-size: 0.8rem;
  padding-top: 1rem;
  
`
const Domain = styled.div`
  color: gray;
  margin-top: 0.5rem;
`
const ClearImage = styled.div`
    clear: both;
`
const LinkOpenGraphForm = ( {url, title, domain, description, image, imageUrl}) => {
  return (
    <HoverBox css={cssBox}>
      <a href={url} target="_blank" rel="noreferrer">
        <div>
          <div css={cssImageWrapper}>
            { (!!image?.childImageSharp ) ? <GatsbyImage image={image.childImageSharp.gatsbyImageData} /> :
             (imageUrl && (<img src={imageUrl} alt='cover image' />))
            }
          </div>
          <div style={{marginLeft: imgSize, paddingLeft: "16px", paddingTop: "16px"}}>
            <Title>{title }</Title>
            <Description>{description?.substr(0,100)}</Description>
            <Domain>{domain}</Domain>
            <ClearImage />
          </div>
        </div>
      </a>
    </HoverBox>

  )
}


const LinkOpenGraph = ({ url, formatter = LinkOpenGraphForm }) => {
  const query = graphql`
    {
    allAksOgLink {
      nodes {
        id
        title, domain, description, imageUrl, url, 
        image {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  }
    `
  const data = useStaticQuery(query)
  const node = data.allAksOgLink.nodes.find(v => v.url === url)
  if (!node) {
    console.log(`LinkExternal: no such url. registere it i src/data/links.yaml:
    - url: ${url}`)
    return LinkOpenGraphForm({ title: url, url, domain: new URL(url).hostname })
  }
  //const { title, url, domain, description, image, imageUrl} = node
  //return LinkOpenGraphForm( node ) 
  return formatter(node)
}

export default LinkOpenGraph
